function ProdukInfo(props) {
  const {kategori, title, isDiskon}=props;
  const spesifikasi = ["Chipset: Apple A14 Bionic (5 nm)", "RAM: 4 GB", "Kamera Belakang: 12 MP (f/1.6, lensa wide 26 mm, dual pixel PDAF, OIS) + 12 MP (f/2.4, lensa ultrawide 120 derajat 13 mm)", "Kamera Depan: 12 MP (f/2.2, lensa wide 23 mm, SL 3D (sensor depth dan biometrik), HDR, gyro-EIS)", "Baterai: 2.815 mAh (fast charging 20W)"];
  const listItems = spesifikasi.map((Listspesifikasi) =>
    <li key={Listspesifikasi}>{Listspesifikasi}</li>
  );
  return (
    <div className="Desc">
        <p className="Cate">{kategori}</p>
        <h1 className="Title">{title}</h1>
        <CheckDiskon  diskon={10} isDiskon={isDiskon}/>
        <p className="Price">IDR 13.999.999</p>
        <p className="Info">
        A14 Bionic jauh lebih cepat dibanding chip ponsel pintar lainnya. 
        Sistem kamera Pro yang menghadirkan fotografi pencahayaan rendah ke tingkat lebih tinggi â€” 
        bahkan dengan peningkatan yang signifikan di iPhone 12 Pro Max. Dan dengan Ceramic Shield yang memiliki ketahanan jatuh empat kali lebih baik. 
        Lihat apa yang bisa dilakukan iPhone baru ini.
        </p>
        <p className="Price">Spesifikasi</p>
        <ul>
          {listItems}
        </ul>
        <br></br>
        <a onClick={(e) => Beli(title,e)}  className="button" href="#">Beli sekarang</a>
      </div>
  );
}




function CheckDiskon(props) {
  const {isDiskon, diskon} = props;

  if (isDiskon=="ada") { 
    return (
      <p>Diskon Promo {diskon}% </p>
      );
    }

    else if (isDiskon=="akanAda") {
      return (
        <p>Segera ada diskon</p>
      );
    }
    else {
      return (
        <p></p>
      );
    
    }
}


function Beli(e) {

  return (
    console.log ("Berhasil Membeli " +e)
  );
}


export default App;
