import logo from './logo.svg';
import './App.css';

function Review(){

    // Dummy Json
  
    const user= [
      {
      "id":1,
      "foto":"https://images.pexels.com/photos/5384429/pexels-photo-5384429.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940",
      "nama":"Randy Farhan",
      "review" : "Gokil banget barangnya, bakal beli lagi disini. Keren !."
      },
      {
        "id":2,
        "foto":"https://images.pexels.com/photos/5397723/pexels-photo-5397723.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940",
        "nama":"Yudian Vantoni",
        "review" : "iPhone Jelek kurang bagus, Bagusan Xiaomi. Kerenan DOTA"
        },
        {
          "id":3,
          "foto":"https://images.pexels.com/photos/1933873/pexels-photo-1933873.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940",
          "nama":"Virgiawan Miko ",
          "review" : "Semoga hp baru saya gak kaya xiaomi saya, tombol power nya susah banget di pencet"
          },
          {
            "id":4,
            "foto":"https://images.pexels.com/photos/4927361/pexels-photo-4927361.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940",
            "nama":"Venita Febrianti",
            "review" : "Seneng banget bisa punya hp ini deh. bisa flexing sama teman teman"
            },
            {
              "id":5,
              "foto":"https://images.pexels.com/photos/4810369/pexels-photo-4810369.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940",
              "nama":"Moses Xavario",
              "review" : "Gabisa pake aplikasi bajakan nihh, app store harus beli"
              }
    ];
  
    const listItems = user.map((itemReview) =>
    
        <div key={itemReview.id} className="itemReview">
          <img src={itemReview.foto} />
        <div className="itemUser">
           <h3>{itemReview.nama}</h3>
            <p>{itemReview.review}</p>
    </div>
    </div>
  );
  
    return (
      <div className="Review">
      <h2>Review Pembeli</h2>
      {listItems}
      </div>
    );
  
  }

  export default Review;